#selected{
    padding: 10px 0 10px 0;
    display: grid;
    grid-template-columns: auto auto 4%;
}
#selected > div{
    display: flex;
    align-items: center;
}
#selected .selected-right{
    justify-content: flex-end;
}
#selected > button{
    padding: 0;
}
.select-color-box > button{
    padding: 2px;
}
#search-select{
    display: flex;
    align-items: center;
    justify-content: center;
}
#search-select > div{
    width:100px;
    margin: 5px;
}

#search-tab{
    padding: 3px 20px 5px 20px;
}
.search-tab-title{
    margin-bottom: 10px;
}
#exchange-tab .search-exchange{
    width: 80%;
}
.chart-control-btns{
    display: grid;
    grid-template-columns: 10% 80% 10%;
}

.select-period-btns > div > div{
    width: 100%;
}